import React, { useState, useEffect } from "react";

const TableDataHeader = ({ tableHeader,setTableHeader }) => {
 
  return (
    <thead>
      <tr>
      
        {tableHeader.map((header) => (
         header.status&& <th>{header.name}</th>
        ))}
      </tr>
    </thead>
  );
};

export default TableDataHeader;
